import axios from "axios";
import "bootstrap";
import { reactive, onMounted, toRef} from "vue";

const url = "/IconButtonList.json";

export default {
  setup() {

    const data = reactive({
      newsdata: "",
    });
    const IconButtonName = toRef(data.newsdata, "IconButtonName");
    onMounted(() => {
      axios.get(url).then((res) => {
        // console.log(res.data);
        data.newsdata = res.data;
      });
    });

    const tabs = [
      {
        Phonics: [
          {
            id: "kidguarden",
            title: "幼兒園",
          },
        ],

        Reading: [
          {
            id: "kidguarden",
            title: "幼兒園",
          },
          {
            id: "Primary",
            title: "國小",
          },
          {
            id: "highs",
            title: "國高中",
          },
        ],
      },
    ];
      
    return {
      data,
      IconButtonName,
      tabs
    };
  },
};